.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#nav {
  padding: 30px;
  a {
    font-weight: bold;
    color: #a9a9a9;
    &.router-link-exact-active {
      color: #23d160;
    }
  }
}
#footer {
    padding-bottom: 30px;
    color: rgba(255, 255, 255, 0.8);
}
#nav a {
    font-weight: 700;
    color: #a9a9a9;
}
html {
    -webkit-tap-highlight-color: transparent;
    background: #1c1f22 !important;
    font: 1em/1 Lato,sans-serif;
    color: hsla(0,0%,100%,.8);
    text-align: center;
}
.tagy{
      -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    border-radius: 5px;
    background: #1c1f22 !important;
    color: hsla(0,0%,100%,.8);
    display: -ms-inline-flexbox;
    display: inline-flex;
    font-size: 0.75rem;
    height: 2em;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding-left: 0.75em;
    padding-right: 0.75em;
}
.boxy{
  font: inherit;
    color: inherit;
    background-color: hsla(0,0%,100%,.0) !important;
    /*border: 1px solid rgba(255,255,255,0) !important;*/
    border-radius: .25em;
    box-shadow: inset 0 0 0 0 rgba(255,255,255,.05) !important;
    transition: box-shadow .3s,border .15s ease-in;
    outline: none;
    padding: .25rem !important;
    margin: 0.5rem 0 !important;
}
.league {
    background-color: #222629;
    border-radius: 6px;
    -webkit-box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
    box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
    color: #4a4a4a;
    display: block;
    padding: 1.25rem;
    margin-bottom: 2rem;
}
.boxy:not(:last-child) {
    border-bottom: 2px solid hsl(210, 10%, 12%);
    border-radius: 0 !important;
}
.saving span:nth-child(2){-webkit-animation-delay:.2s;animation-delay:.2s}
.saving span{font-size:25px}
.saving span{
  -webkit-animation-name:blink-data-v-043efb18;
  animation-name:blink-data-v-043efb18;
  -webkit-animation-duration:1.4s;
  animation-duration:1.4s;
  -webkit-animation-iteration-count:infinite;
  animation-iteration-count:infinite;
  -webkit-animation-fill-mode:both;
  animation-fill-mode:both
  }
.tag:not(body).is-success {
    background-color: #23d160;
    color: #1c1f22 !important;
}
.spinr {
    color: #23d160 !important;
    margin-top: 5rem;
}
